.title {
    font-size: 45px;
    font-weight: 700;
    color: #000;
    width: 70%;
    line-height: 45px;
    float: left;
    margin-bottom: 20px;
}
.reward {
    width: 25% !important;
    height: 50%;
    float: left;
    text-align: center;
}
.main {
    margin: 0;
    padding: 0;
}

.main::after {
    content: "" !important;
    clear: both !important;
    display: table !important;
}

.dscr {
    margin: 20px 0;
    width: 60%;
    font-size: 17px;
    color: #959595;
}

.arrow {
    float: right !important;
    width: 30%;
    height: 80px !important;
}

.arrow::after {
    content: "";
    clear: both;
    display: table;
}


@media (max-width: 767px) {
    .dscr {
        display: none;
    }
    .title , .arrow {
        float: none;
    }
    .title {
        font-size: 28px;
        font-weight: 700;
        color: #000;
        width: 70%;
        line-height: 27px;
        float: left;
        margin-bottom: 20px;
    }
    .arrow {
        display: inline-block;
        width: 100%;
        height: 80px !important;
    }

    .arrow::after,
    .arrow::before,
    .title::after,
    .title::before
     {
        content: "";
        clear: both;
        display: table;
    }
}


