.member:hover {
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(255,0,0,0)), to(rgba(2, 2, 4, 0.6)));
    background: -webkit-linear-gradient(top, rgba(255,0,0,0), rgba(2, 2, 4, 0.6));
    background: -o-linear-gradient(top, rgba(255,0,0,0), rgba(2, 2, 4, 0.6));
    background: linear-gradient(to bottom, rgba(255,0,0,0), rgba(2, 2, 4, 0.6));
    z-index: 2;
}
.member {
    width: 25% !important;
    height: 50% !important;
    float: left;
    text-align: center;
    position: relative;
    z-index: 1;
}

.member:hover > .memImage {
    z-index: -3;
    position: relative;
    max-height:100%;
    max-width:100%;
    height:auto !important;
    width:auto !important;
    margin: auto;
    display: block;
}

.position {
    display: none;
}
.name {
    display: none;
}
.projects {
    display: none;
}
.projectsNumber {
    display: none;
}
.experience {
    display: none;
}
.experienceNumber {
    display: none;
}

.member:hover > .position {
    position: absolute;
    color: #CCC9D1;
    font-weight: 700;
    font-size: 12px;
    top: 60%;
    left: 10%;
    display: block;
}



.member:hover > .name {
    position: absolute;
    color: #fff;
    font-size: 30px;
    font-weight: 700;
    top: 70%;
    left: 10%;
    font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
    display: block;
}

.name::after {
    content: "";
    clear: both;
    display: table;
}
.member:hover > .projects {
    position: absolute;
    color: #CCC9D1;
    font-weight: 700;
    font-size: 10px;
    top: 85%;
    float: left;
    display: block;
    left: 10%;
}
.member:hover > .projectsNumber {
    position: absolute;
    color: #B49235;
    font-weight: 700;
    font-size: 22px;
    top: 88%;
    display: block;
    float: left;
    left: 10%;
}

.member:hover > .experience {
    position: absolute;
    color: #CCC9D1;
    font-weight: 700;
    display: block;
    font-size: 10px;
    top: 85%;
    float: right;
    left: 60%;
}

.member:hover > .experienceNumber {
    position: absolute;
    color: #B49235;
    font-weight: 700;
    display: block;
    font-size: 22px;
    top: 88%;
    float: right;
    left: 60%;
}


