}

.bgImg {
    z-index: -2;
    position: absolute;
    top: 0;
    left:0;
    width: 50vw;
    height: 100vh;
    overflow: hidden;
}

.layer {
    background-color: rgba(2, 2, 4, 0.426);
    z-index: -1;
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;
}
.cases {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100vh;
    position: relative;
    overflow: hidden;
    background-position: center;
    background-size: cover;
}

.case {
    width: 33.3%;
    height: 100%;
    position: relative;
    overflow: hidden;
    float: left;
    background-position: center;
    background-size: cover;
}

.caseType {
    color: #FFCE4B;
    font-size: 10px;
    letter-spacing:1.5px;
    font-weight: bold;
    position: absolute;
    top: 36%;
    left: 40%;
}

.caseTitle {
    color: #fff;
    font-weight: bold;
    font-size: 30px;
    position: absolute;
    top: 44%;
    left: 18%;
    text-align: center;
}

.caseDscr {
    color:#C1BCBF;
    position: absolute;
    font-weight: bold;
    font-size: 16px;
    letter-spacing:0.5px;
    top: 52%;
    left: 14%;
}

.button {
    color: #000;
    font-weight: bold;
    height: 50px;
    width: 250px;
    background-color: #FFDB4B;
    position: absolute;
    display: inline-block;
    line-height: 50px;
    text-transform: uppercase;
    text-align: center;
    top: 65%;
    left: 15%;
}

.link {
    overflow: hidden;
    position: relative;
}

.link:hover  .button {
    -webkit-animation: button-in 500ms forwards;
            animation: button-in 500ms forwards;
}

.link:not(hover) .button {
    -webkit-animation: button-out 500ms forwards;
            animation: button-out 500ms forwards;
}

.link:hover  .case {
    -webkit-animation: background-in 10s both alternate;
            animation: background-in 10s both alternate;
}

.button:hover {
    background-color: #F0CE3D;
}

@-webkit-keyframes button-in {
    0% {
        position: absolute;
        top: 70%;
        left: 15%;
        opacity: 0;
    }

    100% {
        position: absolute;
        top: 65%;
        left: 15%;
        opacity: 1;
    }
}

@keyframes button-in {
    0% {
        position: absolute;
        top: 70%;
        left: 15%;
        opacity: 0;
    }

    100% {
        position: absolute;
        top: 65%;
        left: 15%;
        opacity: 1;
    }
}

@-webkit-keyframes button-out {
    0% {
        position: absolute;
        top: 65%;
        left: 15%;
        opacity: 1;
    }

    100% {
        position: absolute;
        top: 70%;
        left: 15%;
        opacity: 0;
    }
}

@keyframes button-out {
    0% {
        position: absolute;
        top: 65%;
        left: 15%;
        opacity: 1;
    }

    100% {
        position: absolute;
        top: 70%;
        left: 15%;
        opacity: 0;
    }
}


