.block {
    width: 30%;
    float: left;
    height: 150px;
}
.imageDiv {
    position:relative;
    width: 100%;
    height: 70%;
}
.img {
    position:absolute;
    top: 20%;
    width: 50%;
    /*height:50%;*/
    -webkit-filter: grayscale(98%);
            filter: grayscale(98%);
}
.img:hover {
    width: 50%;
    /*height:50%;*/
    -webkit-filter: grayscale(0);
            filter: grayscale(0);
}

.title {
    text-transform: uppercase;
    font-size: 14px;
    width: 100%;
    font-weight: bold;
}
.subTitle {
    text-transform: uppercase;
    font-size: 10px;
    width: 70%;
    font-weight: bold;
}


@media (max-width: 3000px) {
    .block {
        width: 20%;
        float: left;
        height:150px;
    }

    .img {
        width: 50%;
        /*height: 50%;*/
        background-repeat: no-repeat;
        -webkit-filter: grayscale(98%);
                filter: grayscale(98%);
    }
    .img:hover {
        width: 50%;
        /*height: 50%;*/
        background-repeat: no-repeat;
        -webkit-filter: grayscale(0);
                filter: grayscale(0);
    }

    .title {
        text-transform: uppercase;
        font-size: 14px;
        width: 100%;
        font-weight: bold;
    }
    .subTitle {
        text-transform: uppercase;
        font-size: 10px;
        width: 70%;
        font-weight: bold;
    }
}

@media (max-width: 1400px) {
    .block {
        margin: 25px 0;
        width: 25%;
        float: left;
        height:150px;
    }
    .img {
        width: 60%;
        /*height: 50%;*/
        background-repeat: no-repeat;
        -webkit-filter: grayscale(98%);
                filter: grayscale(98%);
    }
    .img:hover {
        width: 60%;
        /*height: 50%;*/
        background-repeat: no-repeat;
        -webkit-filter: grayscale(0);
                filter: grayscale(0);
    }
    .title {
        text-transform: uppercase;
        margin-top: 15px;
        font-size: 14px;
        width: 100%;
