}

@media (max-width: 1280px) {
    .block {
        margin: 25px 0;
        width: 33.3%;
        float: left;
        height:150px;
    }
    .img {
        width: 60%;
        /*height: 50%;*/
        background-repeat: no-repeat;
        -webkit-filter: grayscale(98%);
                filter: grayscale(98%);
    }
    .img:hover {
        width: 60%;
        /*height: 50%;*/
        background-repeat: no-repeat;
        -webkit-filter: grayscale(0);
                filter: grayscale(0);
    }
