@media (max-width: 767px) {
    .block {
        margin: 25px 0;
        width: 50%;
        float: left;
        height:150px;
    }
    .img {
        width: 60%;
        /*height: 50%;*/
        background-repeat: no-repeat;
        -webkit-filter: grayscale(98%);
                filter: grayscale(98%);
    }
    .img:hover {
