@media (max-width: 480px) {

    .block {
        margin: 18px 0;
        width: 100%;
        float: left;
        height: 80px;
    }
    .imageDiv {
        position:relative;
        width: 100%;
        height: 90%;
    }
    .img {
        margin-right: 10px;
        width: 40%;
        height: auto;
        float: left;
        -webkit-filter: grayscale(98%);
                filter: grayscale(98%);
    }
    .img:hover {
        margin-right: 10px;
        width: 50%;
        height: 100%;
        float: left;
        -webkit-filter: grayscale(0);
                filter: grayscale(0);
    }
    .link {
        font-size: 12px;
    }
    img {
        margin-right: 10px;
        width: 100%;
        height: 100%;
        float: left;
    }
    .title {
        font-size: 12px;
        width: 100%;
        font-weight: bold;
        text-transform: uppercase;
        margin-top: 0;
    }
    .subTitle {
        font-size: 8px;
        width: 100%;
        font-weight: bold;
        text-transform: uppercase;
    }
}


