.banner {
    height: 35vw;
    width: 100%;
    background-size: cover;
    background: no-repeat center;
    position: relative;
    font-family: NeoSansPro, sans-serif ;
    font-style: normal;
    font-weight: 400;
}

/*@font-face {*/
    /*font-family: NeoSansPro;*/
    /*src: url(fonts/NeoSansPro-Medium.woff);*/
/*}*/

.layer {
    background-color: rgba(2, 2, 4, 0.426);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.bannerInnerLeft {
    top: 12vw;
    left: 10vw;
    position: absolute;
    color: #ffff;
    font-size: 45px;
    font-family: NeoSansPro, sans-serif ;
    font-weight: 700;
    text-align: center;
}

.subTitle {
    font-size: 20px;
}

.bannerInnerRight {
    top: 12vw;
    right: 10vw;
    position: absolute;
    color: #ffff;
    font-size: 45px;
    font-family: NeoSansPro, sans-serif ;
    font-weight: 700;
    text-align: center;
}

.bannerInnerLink {
    text-decoration: none;
    color: #ffff;
    font-size: 45px;
    font-family: NeoSansPro, sans-serif ;
    font-weight: 700;
}

.bannerInnerLink:hover {
    text-decoration: none;
    color: #ffff;
    font-family: NeoSansPro, sans-serif ;
    font-weight: 700;
}

.bannerInnerLink:focus {
    text-decoration: none;
    color: #ffff;
    font-family: NeoSansPro, sans-serif ;
    font-weight: 700;
}

.bannerInnerCntr {
    top: 42%;
    left: 49%;
    position: absolute;
    color: #ffff;
    font-size: 45px;
    font-family: NeoSansPro, sans-serif ;
    font-weight: 700;
}

