.block {
    width: 210px;
    float: left;
    height: 30px;
    margin-bottom: 5px;
    position: fixed;
    bottom: 130px;
    right: -70px;
    -webkit-transform: rotate(-90deg);
        -ms-transform: rotate(-90deg);
            transform: rotate(-90deg)
}

.block:nth-of-type(even)::after {
    content: "";
    clear: both;
    display: table;
}

.innerBlock {
    width: 75%;
}

.dscr {
    color: #959595;
}

.title {
    font-size: 14px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    margin-bottom: 10px;
    float: left;
}

.arrow {
    margin-left: 5px;
    margin-top: 8px;
    width: 40px;
    height: 12px;
    border-top: 2px solid #000;
    position: relative;
    float: left;
}

.link {
    text-decoration: none;
    color: #000;
}

.arrow::after {
    content: "";
    position: absolute;
    top: -1px;
    left: 100%;
    margin-left: -6px;
    border: 2px solid;
    border-bottom-width: 0;
    border-left-width: 0;
    width: 8px;
    height: 8px;
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
    -webkit-transform-origin: top right;
        -ms-transform-origin: top right;
            transform-origin: top right;
}
.block:hover {
    cursor: pointer;
}
.block:hover>.innerBlock>.link {
    text-decoration: none;
    cursor: pointer;
}

.innerBlock:hover>.link {
    color: #FFDB4B;
    cursor: pointer;
    text-decoration: none;
    cursor: pointer;
}

.innerBlock:hover>.link>.dscr {
    color: #FFDB4B;
    cursor: pointer;
    text-decoration: none;
    cursor: pointer;
}

.link:hover>.arrow {
    margin-left: 5px;
    margin-top: 8px;
    width: 100px;
    height: 12px;
    border-top: 2px solid #FFDB4B;
    position: relative;
    -webkit-transition: width 300ms ease;
    -o-transition: width 300ms ease;
    transition: width 300ms ease;
    color: #FFDB4B;
    cursor: pointer;
    
}

.block:hover .arrow {
    margin-left: 5px;
    margin-top: 8px;
    width: 100px;
    height: 12px;
    border-top: 2px solid #FFDB4B;
    position: relative;
    -webkit-transition: width 300ms ease;
    -o-transition: width 300ms ease;
    transition: width 300ms ease;
    color: #FFDB4B;
    cursor: pointer;
}

.block:hover .link {
    color: #FFDB4B;
    cursor: pointer;
}

/* 
.block:hover > .innerBlock > .link > .arrow {
    margin-top: 20px;
    width: 100px;
    height: 12px;
    border-top: 2px solid #FFDB4B;
    position: relative;
    transition: width 300ms ease;
    color: #FFDB4B;
    cursor: pointer;
} */

.link:not(hover)>.arrow {
    position: relative;
    width: 40px;
    -webkit-transition: width 300ms ease;
    -o-transition: width 300ms ease;
    transition: width 300ms ease;
}

.link:focus {
    text-decoration: none;
    color: #000;
}

@media (max-width: 480px) {
    .block {
        width: 100%;
    }
    .title {
        font-size: 25px;
        font-family: Arial, Helvetica, sans-serif;
        font-weight: bold;
    }
}

@media (max-width: 480px) {
    .block {
        width: 100%;
        height: 100%;
        margin-bottom: 5px;
    }
    .title {
        font-size: 25px;
        font-family: Arial, Helvetica, sans-serif;
        font-weight: bold;
    }
}


@media (max-width: 1025px) {
    .block {
        display: none;
    }
}


