.block {
    width: 50%;
    float: left;
    height: 200px;
    margin-bottom: 25px;
}

.block:nth-of-type(even)::after {
    content: "";
    clear: both;
    display: table;
}

.innerBlock {
    width: 85%;
}

.dscr {
    color: #959595;
    font-size: 16px;
}


.title {
    font-size: 20px !important;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    margin-bottom: 10px;
}

.arrow {
    margin-top: 20px;
    width: 40px;
    height: 12px;
    border-top: 2px solid #000;
    position: relative;
}

.link {
    text-decoration: none;
    color: #000;
}

.arrow::after {
    content: "";
    position: absolute;
    top: -1px;
    left: 100%;
    margin-left: -6px;
    border: 2px solid;
    border-bottom-width: 0;
    border-left-width: 0;
    width: 8px;
    height: 8px;
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
    -webkit-transform-origin: top right;
        -ms-transform-origin: top right;
            transform-origin: top right;
}

.block:hover > .innerBlock > .link{
    text-decoration: none;
}

.innerBlock:hover>.link {
    color: #FFDB4B;
    cursor: pointer;
    text-decoration: none;
}

.innerBlock:hover>.link > .dscr {
    color: #FFDB4B;
    cursor: pointer;
    text-decoration: none;
}

.link:hover > .arrow {
    margin-top: 20px;
    width: 100px;
    height: 12px;
    border-top: 2px solid #FFDB4B;
    position: relative;
    -webkit-transition: width 300ms ease;
    -o-transition: width 300ms ease;
    transition: width 300ms ease;
    color: #FFDB4B;
    cursor: pointer;
}
