.link:not(hover)>.arrow {
    position: relative;
    width: 40px;
    -webkit-transition: width 300ms ease;
    -o-transition: width 300ms ease;
    transition: width 300ms ease;
}

.link:focus {
    text-decoration: none;
    color: #000;
}


@media (max-width: 767px) {
    .title {
        font-size: 22px !important;
        font-family: Arial, Helvetica, sans-serif;
        font-weight: bold;
    }
    .dscr {
        font-size: 14px;
        line-height: 17px;
        font-weight: 500;
        color: #959595;
    }

}

@media (max-width: 480px) {
    .block {
        width: 100%;
        height: 100%;
        margin-bottom: 20px;
    }
    .innerBlock {
        width: 96%;
    }
    .dscr {
        font-size: 12px;
        width: 80%;
        line-height: 16px;
        font-weight: 500;
        color: #959595;
    }
    .title {
        font-size: 22px !important;
        font-family: Arial, Helvetica, sans-serif;
        font-weight: bold;
    }
    .arrow {
        margin-top: 10px;
        width: 20px;
        height: 12px;
        border-top: 2px solid #000;
        position: relative;
    }
}




