.block {
    width: 30%;
    float: left;
    height: 150px;
}

.img {
     width: 50%;
     height:50%;
     -webkit-filter: grayscale(98%);
             filter: grayscale(98%);
 }
.img:hover {
    width: 50%;
    height:50%;
    -webkit-filter: grayscale(0);
            filter: grayscale(0);
}

.title {
    text-transform: uppercase;
    font-size: 14px;
    width: 100%;
    font-weight: bold;
}
.subTitle {
    text-transform: uppercase;
    font-size: 10px;
    width: 70%;
    font-weight: bold;
}


