.container {
    width: 100%;
    height:800px;
    margin-top: 120px;
    margin-bottom: 40px;
    background-color: #272937;
    color: #fff;
    padding: 100px 0;
    display: block;
}
.container::after {
    content: "" !important;
    clear: both !important;
    display: table !important;
}
.container::before {
    content: "" !important;
    clear: both !important;
    display: table !important;
}
.header {
    margin-top: 30px;
    margin-bottom: 20px;
}

.header::after {
    content: "";
    clear: both;
    display: table;
}
.decor {
    width: 25px;
    height: 8px;
    background-color: #fff;
    border: 0;
    float: left;
    margin-right: 15px;
}

.title {
    margin-top: -5px;
    margin-left: 50px;
    color: #fff;
    width: 25%;
    display: block;
    font-size: 18px;
    font-weight: 700;
    float: left;
    text-transform: uppercase;
}


.main {
    padding-left: 85px;
}

.title1 {
    font-size: 55px;
    font-weight: 700;
    color: #fff;
    width: 70%;
    line-height: 55px;
    margin: 50px 0;
}
.dscr {
    margin: 100px 0 0 0;
    width: 60%;
    font-size: 17px;
}

.cases {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100vh;
    position: relative;
    overflow: hidden;
    background-position: center;
    background-size: cover;
}

.case {
    width: 33.3%;
    height: 100%;
    position: relative;
    overflow: hidden;
    float: left;
    background-position: center;
    background-size: cover;
}

.caseType {
    color: #FFCE4B;
    font-size: 10px;
    letter-spacing: 1.5px;
    font-weight: bold;
    position: absolute;
    top: 36%;
    left: 40%;
}

.caseTitle {
    color: #fff;
    font-weight: bold;
    font-size: 30px;
    position: absolute;
    top: 44%;
    left: 18%;
    text-align: center;
}

.button {
    color: #000;
    font-weight: bold;
    height: 50px;
    width: 250px;
    background-color: #FFDB4B;
    display: inline-block;
    line-height: 50px;
    text-transform: uppercase;
    text-align: center;
    top: 65%;
    left: 15%;
    border: none;
}

.link {
    overflow: hidden;
}

.link:hover .button {
    -webkit-animation: button-in 500ms forwards;
            animation: button-in 500ms forwards;
}

.link:not(hover) .button {
    -webkit-animation: button-out 500ms forwards;
            animation: button-out 500ms forwards;
}

.link:hover .case {
    -webkit-animation: background-in 10s both alternate;
            animation: background-in 10s both alternate;
}

.button:hover {
    background-color: #F0CE3D;
}

.contact {
    margin-top: 70px;
}

.contactLink {
    text-decoration: none;
    color: #fff;
    font-weight: bold;
}

.contactLeft {
    float: left;
    width: 40%;
}

.contactRight {
    float: right;
    width: 60%;
}

span {
    color: #AFAFB5;
}

.contactLink:hover {
    text-decoration: none;
    color: #FFDB4B;
    font-weight: bold;
}

.contactLink:focus {
    text-decoration: none;
    color: #FFDB4B;
    font-weight: bold;
}

.contactLink:active {
    text-decoration: none;
    color: #FFDB4B;
    font-weight: bold;
}

