.banner {
    height: 35vw;
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    position: relative;
    text-align: center;
}


.bannerInner {
    text-align: center;
    height: 50%;
    width: 50%;
    margin: 7% auto;
}


.caseType {
    color: #FFCE4B;
    margin-bottom: 20px;
    letter-spacing: 1.5px;
    font-size: 16px;
    font-weight: 500;
    text-transform: uppercase;
}

.layer {
    text-align: center;
    background-color: rgba(2, 2, 4, 0.426);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.subTitle {
    font-size: 20px;
    line-height: 23px;
}

.bannerInnerLink {
    text-decoration: none;
    color: #ffff;
    font-size: 55px;
    top: 35%;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 700;
}

.order {
    background-color: #FFDB4B;
    border: 0;
    width: 220px;
    height: 50px !important;
    color: #000;
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    margin: 30px 5px 0 5px;
}

.review {
    background-color: transparent;
    border: 1px solid #B6B4B2;
    width: 240px;
    height: 50px !important;
    color: #fff;
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    margin: 30px 5px 0 5px;
}
.price {
    background-color: transparent;
    border: 1px solid #B6B4B2;
    width: 240px;
    height: 50px !important;
    color: #fff;
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    margin: 30px 5px 0 5px;
}

.review:hover {
    border: 1px solid #fff;
}
.price:hover {
    border: 1px solid #fff;
}

.bannerInnerLink:hover {
    text-decoration: none;
    color: #ffff;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 700;
}

.bannerInnerLink:focus {
    text-decoration: none;
    color: #ffff;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 700;
}

.bannerInnerCntr {
    top: 42%;
    left: 49%;
    position: absolute;
    color: #ffff;
    font-size: 45px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 700;
}

