.content {
    width: 100%;
    height: 100%;
    position: relative;
}

@media (min-width: 1020px) {
    .content {
        top: 10rem;
    }
}

@media (max-width: 1020px) {
    .content {
        top: 4rem !important;
    }
}

@media (max-width: 3000px) {
    .content {
        top: 10rem !important;
    }
}

@media (max-width: 1270px) {
    .content {
        top: 4rem !important;
    }
}

@media (max-width: 480px) {
    .content {
        top: 3.6rem !important;
    }
}

@media (max-width: 350px) {
    .content {
        top: 3.6rem !important;
    }
}


@media (min-width: 1020px) {
    .Container {
        width: 84%;
        margin: 0 auto;
        padding: 0;
    }
}

@media (max-width: 1020px) {
    .Container {
        margin: 0;
        padding: 0;
        width: 100% !important;
        font-size: 16px;
    }
}
@media (max-width: 1200px) {
    .Container {
        margin: 0 auto;
        padding: 0;
        width: 86% !important;
        font-size: 16px;
    }
}

@media (max-width: 1050px) {
    .Container {
        margin: 0 auto;
        padding: 0;
        width: 90% !important;
        font-size: 16px;
    }
}

@media (max-width: 1025px) {
    .Container {
        margin: 0;
        padding: 0;
        width: 100% !important;
        font-size: 16px;
    }
}


