.container {
    padding: 10px 20px;
    color: #000000;
}

.form_element {
    margin: 20px 0px;
}

.form_element label {
    display: block;
    margin-bottom: 5px;
    font-size: 20px;
    font-weight: 300;
}

input {
    font-size: 16px;
    padding: 8px 12px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    color: #000;
}

textarea {
    color: #000;
}

button {
    background: #3F51B5;
    color: #fff;
    font-size: 15px;
    padding: 10px 20px;
}

select {
    width: 400px;
    padding: 5px 8px;
    height: 50px;
    font-size: 16px;
    color: #000;
}

.select {
    color: #000000;
}

.required {
    background-color: #FF0000;
    color: #fff;
}

.label_error {
    color: #FF5722;
    font-weight: 600;
    margin-top: 10px;
}


