/* __________________________________________________________ */


@media (max-width: 3000px) {
    .nav {
        font-size: 14px;
        letter-spacing: 0.5px;
        padding: 2rem 0;
        display: block;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        height: 10rem;
        line-height: 6rem;
    }

    .nav::after {
        content: "";
        clear: both;
        display: table;
    }

    .navList {
        margin: auto;
    }

    .navList>li {
        display: block;
        list-style-type: none;
        font-family: Arial, Helvetica, sans-serif;
    }

    .navLink,
    .logo {
        text-decoration: none;
        float: left;
        margin-right: 4rem;

        position: relative;
    }
    .logoImg {
        height:  3.6rem !important;
        margin-right: 10px !important;
        margin-top: 0 !important;
        line-height: 6rem !important;
        width: auto !important;
        padding: 0 !important;
    }
    .logo {
        height: 3.6rem !important;
        line-height: 6rem !important;
        margin-top: 0 !important;
        margin-right: 0 !important;
        width: auto !important;
        padding: 0 !important;
    }

    .navLink {
        text-decoration: none;
        color: #000000;
        font-weight: 700;

        position: relative;
    }

    .navLink:hover {
        color: #FFDB4B;
        text-decoration: none;
    }

    .navLinkNum:hover {
        text-decoration: none;
        color: #000000;
    }

    .navLinkRight:hover {
        text-decoration: none;
        color: #000000;
    }

    .yelBtn:hover {
        background-color: #FFD631;
        text-decoration: none;
    }

    .navLink:focus {
        color: #FFDB4B;
        text-decoration: none;
    }

    .navLinkNum:focus {
        text-decoration: none;
        color: #000000;
    }

    .navLinkRight:focus {
        text-decoration: none;
        color: #000000;
    }

    .yelBtn:focus {
        background-color: #FFD631;
        text-decoration: none;
    }

    .navLinkNum {
        float: right;
        color: #000000;
        font-weight: 700;
        margin-right: 3rem;
    }

    .navLinkRight {
        float: right;
        color: #000000;
        font-weight: 700;
    }

    .yelBtn {
        background-color: #FFDB4B;
        height: 6rem;
        width: 18rem;
        font-weight: 700;
        top: 50%;
        margin-top: 0;
        text-align: center;
        line-height: 6rem;
    }
}

@media (max-width: 1270px) {
    .logo {
        height: 2.7rem;
    }
    .nav {
        font-size: 13px;
        letter-spacing: 0.5px;
        padding: 2rem 0;
        display: block;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;

        height: 7rem;
        line-height: 3rem;
    }
    .navLink,
    .logo {
        float: left;
        margin-right: 2rem;
    }
    .navLink {
        color: #000000;
        font-weight: 700;
    }

    .logoImg {
        height: 2.7rem !important;
        margin-right: 10px !important;
        margin-top: 0 !important;
        line-height: 4rem !important;
        width: auto !important;
        padding: 0 !important;
    }
    .logo {
        height: 2.7rem !important;
        line-height: 4rem !important;
        margin-top: -8px !important;
        margin-right: 0 !important;
        width: auto !important;
        padding: 0 !important;
    }
    .yelBtn {
        text-decoration: none;
        background-color: #FFDB4B;
        height: 3.5rem;
        width: 15rem;
        font-weight: 700;
        top: 50%;
        margin-top: 0;
        text-align: center;
        line-height: 3.5rem;
        text-decoration: none;
    }

}

@media (max-width: 1020px) {
    .menuBar {
        display: block;
        float: left;
        padding: 14px !important;
    }
    .nav {
        font-size: 13px;
        letter-spacing: 0.5px;
        padding: 0;
        display: block;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        height: 4rem;
        line-height: 4rem;
    }
    .logoImg {
        height: 3rem !important;
        margin-top: 0 !important;
    }
    .logo {
        height: 3rem !important;
        margin-top: 0 !important;
    }
    .navLink {
        display: none;
    }
    .yelBtn {
        background-color: #FFDB4B;
        line-height: 4rem;
        height: 4rem;
        width: 15rem;
        font-weight: 700;
        top: 50%;
        margin-top: 0;
        text-align: center;
    }

}

@media (max-width: 550px) {
    .navLink,
    .navLinkNum {
        font-size: 8px;
        margin-right: 1rem;
    }
}

@media (max-width: 480px) {
    .menuBar {
        display: block;
        float: left;
        padding: 12px !important;
    }
    .nav {
        font-size: 13px;
        letter-spacing: 0.5px;
        padding: 0;
        display: block;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        height: 3.6rem;
        line-height: 3.6rem;
    }
    .logoImg, .logo {
        height: 2.6rem !important;
        margin-right: 0 !important;
        margin-top: 2px !important;
        line-height: 4rem !important;
        width: auto !important;
    }
    .navLink,
    .navLinkNum {
        display: none;
    }
    .yelBtn {
        background-color: #FFDB4B;
        line-height: 4rem;
        height: 3.6rem;
        width: 15rem;
        font-weight: 700;
        font-size: 12px;
        top: 50%;
        margin-top: 0;
        text-align: center;
    }
}


@media (max-width: 370px) {
    .menuBar {
        display: block;
        float: left;
        padding: 12px !important;
    }
    .nav {
        font-size: 13px;
        letter-spacing: 0.5px;
        padding: 0;
        display: block;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        height: 3.6rem;
        line-height: 3.4rem;
    }
    .logoImg, .logo {
        height: 2rem !important;
        margin-right: 0 !important;
        margin-top: 3.5px !important;
        line-height: 4rem !important;
        width: auto !important;
    }
    .navLink,
    .navLinkNum {
        display: none;
    }
    .yelBtn {
        background-color: #FFDB4B;
        line-height: 4rem;
        height: 3.6rem;
        width: 12rem;
        font-weight: 700;
        font-size: 10px;
        top: 50%;
        margin-top: 0;
        text-align: center;
        display:block;
    }
}


