.title {
    font-size: 45px;
    font-weight: 700;
    color: #959595;
    width: 50%;
    line-height: 45px;
    float: left;
    margin-bottom: 20px;
}

.main {
    margin: 0;
    padding: 0;
    color: #959595;
}

.main::after {
    content: "" !important;
    clear: both !important;
    display: table !important;
}

.dscr {
    margin: 20px 0;
    width: 90%;
    font-size: 16px;
    color: #959595;
}

.rewards {
    margin: 40px 0;
}

@media (max-width: 700px) {
    .dscr {
        margin: 20px 0;
        width: 90%;
        font-size: 11px;
        letter-spacing: 0.3px;
    }
}


