.container {
    width: 100%;
    margin: 40px auto;
    position: relative;
}
.container::after {
    content: "" !important;
    clear: both !important;
    display: table !important;
}
.header {
    margin-top: 30px;
    margin-bottom: 20px;
}

.header::after {
    content: "";
    clear: both;
    display: table;
}
.decor {
    width: 25px;
    height: 8px;
    background-color: #000;
    border: 0;
    float: left;
    margin-right: 15px;
}

.title {
    margin-top: -5px;
    font-size: 14px;
    font-weight: 700;
    float: left;
    color: #000;
    text-transform: uppercase;
}


.main {
    padding-left: 40px;
}


@media (max-width: 480px) {
    .title {
        margin-top: -3px;
        font-size: 12px;
        font-weight: 700;
        float: left;
        color: #000;
        text-transform: uppercase;
    }
}

@media (max-width: 320px) {
    .main {
        padding-left: 25px;
    }
    .decor {
        width: 23px;
        height: 8px;
        background-color: #000;
        border: 0;
        float: left;
        margin-right: 5px;
    }
    .title {
        margin-top: -3px;
        font-size: 11px;
        font-weight: 700;
        float: left;
        color: #000;
        text-transform: uppercase;
    }
}


