.button {
    color: #000;
    display: block;
    font-weight: bold;
    height: 50px !important;
    width: 150px;
    padding: 0;
    background-color: #FFDB4B;
    line-height: 50px !important;
    text-transform: uppercase;
    text-align: center;
    border: none;
    cursor: pointer;
}


